import {getOrgData} from '@/api/org';
const state = {
  isSelectOrgShow: false,
  selectedNode: {} // 
};
const mutations = {
  // state
  setState(state, action){
    Object.keys(action).forEach(item => {
      state[item] = action[item];
    });
  }
};

const actions = {
  // 
  async getOrgData(ctx, parentCode = '10000'){
    let result = await getOrgData(parentCode);
    return result.data;
  }
};
export default {
  namespaced: true,
  state,
  mutations,
  actions
};